/*Estilos especificos de la seccion certs*/
#certs {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    /*background: green;*/
    z-index: 500;
}

#cont-certs {
    width: calc(3 * 100vw);
    height: 90%;
    display: flex;
    flex-direction: row;
    /*background: yellow;*/
    margin-top: 6%;
}

.cont-pair {
    width: 50vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*background: orange;*/
}

.frame, .frame-contain {
    width: 86%;
    height: 46%;
    background: #334547;
    margin: 2%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    -webkit-box-shadow: 2px 2px 0px 2px rgba(37, 50, 51, 1);
    -moz-box-shadow: 2px 2px 0px 2px rgba(37, 50, 51, 1);
    box-shadow: 2px 2px 0px 2px rgba(37, 50, 51, 1);
}

.frame img {
    width: 92%;
    height: 90%;
    object-fit: cover;
    margin: auto;
}


.frame-contain {
    
}

.frame-contain img {
    width: 92%;
    height: 90%;
    object-fit: contain;
    margin: auto;
    background: white;
}

.frame:hover, .frame-contain:hover {
    transform: rotate(2deg);
}

@media screen and (orientation: portrait) {
    #cont-certs {
        width: calc(6 * 100vw);
        height: 170vw;
        margin-top: calc((100vh - 170vw)/2);
    }

    .cont-pair {
        width: 100vw;
    }
}