/*Estilos especificos de la seccion skills*/
#skills{
    width: 250%;
    height: 500vh;
    /*background: green;*/
    /*background: linear-gradient(to right, #D8D1CD 50%, transparent 50%);*/
    position: fixed;
    top: 0;
    left: 0;
    z-index: 300;
    /*display: none;*/

    
    background: url(../images/back_desk3.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 15% bottom;
}

#cont-shelf{
    margin: 0 0 0 calc(50vw - 45vh);
    width: 90vh;
    background: darkgray;
    z-index: 302;
    position: relative;
}

.shelf{
    width: 90vh;
    height: 90vh;
    background: url(../images/shelf.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.content-shelf{
    width: 90%;
    height: 90%;
    margin: 5% 5% 1% 5%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
    padding-left: 3%;
}

#back-shelf{
    width: calc(50vw - 45vh);
    height: 200%;
    background: #D8D1CD;
    position: relative;
    top: -100%;
    z-index: 301;
}

@media screen and (orientation: portrait) {
    #skills{
        height: 488vw;
    }

    #cont-shelf{
        margin: 0 0 0 5vw;
        width: 90vw;
    }

    .shelf{
        width: 90vw;
        height: 90vw;
    }

    #back-shelf{
        width: 5vw;
        height: 200%;
    }
}

@media screen and (max-width: 300px) {
    .tag p{
        font-size: 0.4rem !important;
    }    
}

.book img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    filter: brightness(85%);
    -webkit-box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
}

.book:hover img{
    filter: brightness(100%);
    transform: scaleY(1.02);
    -webkit-box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
}

.nobook img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.tag{
    height: 3%;
    width: fit-content;
    background: white;
    /*position: relative;
    top: -3%;*/
    margin: 0 auto 1% auto;
    padding: 0 1%;
}

.tag p{
    font-size: 0.6rem;
    text-align: center;
    color: black;
    margin-top: 0;
}

.tagHidden{
    height: 3%;
    width: fit-content;
    margin: 0 auto 1% auto;
}