/*Estilos especificos del componente screen*/
#cont-screen{
    width: 100%;
    height: 100vh;
    /*background: red;*/
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    overflow: hidden;
}

#screen{
    /*background: green;*/
    margin: 0 auto;
}

#content-screen {
    border-radius: 3px;
    /*background: #1B1C1B;*/
    background: #D3EDD3;
    position: relative;
    margin: 0 auto;

    width: 85%;
    height: 49%;
    top: -90%;
}

#soporte-screen{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

