#scroll{
    position: fixed;
    top: calc(100vh - 150px);
    background: rgba(255, 255, 255, 0.6);
    border-radius: 30px;
    width: 60px;
    height: 90px;
    margin: 0 auto;
    padding: 20px;
    z-index: 998;
    line-height: 15px;
    flex-direction: column;
    align-items: center;
}

#scroll img{
    width: 50px;
    height: 15px;
    object-fit: contain;
}

#scroll p{
    opacity: 0;
    width: fit-content;
    text-align: center;
    color: black;
    font-size: 0.6em;
    font-weight: 500;
    animation: opa4 3s infinite;
    margin: 10px 0 0 0px;
}

#arrow1 {
    opacity: 0;
    animation: opa1 3s infinite;
}

#arrow2 {
    opacity: 0;
    animation: opa2 3s infinite;
}

#arrow3 {
    opacity: 0;
    animation: opa3 3s infinite;
}

@keyframes opa1 {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 1;
    }

    50% {
        opacity: 0.75;
    }

    75% {
        opacity: 0.5;
    }

    100% {
        opacity: 0.25;
    }
}

@keyframes opa2 {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    75% {
        opacity: 0.75;
    }

    100% {
        opacity: 0.5;
    }
}

@keyframes opa3 {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 0;
    }

    50% {
        opacity: 0;
    }

    75% {
        opacity: 1;
    }

    100% {
        opacity: 0.75;
    }
}

@keyframes opa4 {
    0% {
        opacity: 0;
    }

    25% {
        opacity: 0;
    }

    50% {
        opacity: 0;
    }

    75% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}