.App{
    height: 10200vh;
    text-align: center;
}

/*common*/
.horizontal {
    display: flex;
    flex-direction: row;
}

.listaSinDecoraciones {
    list-style: none;
}

.oculto {
    display: none;
}

.img-compo1 {
    position: fixed;
    width: 100%;
    height: 100vh;
    object-fit: contain;
    top: 200px;/**/
  }

/*offset*/
#offset {
    bottom: 10px;
    left: 10px;
    position: fixed;
    z-index: 1000;
    width: fit-content;
    height: fit-content;
    opacity: 0.6;
    /*background: yellow;*/
}

/*home-t*/
#screen-t {
    width: 100%;
    height: 100vh;
    background: #D3EDD3;
    /*background: orange;*/
    z-index: 199;
    top: -100%;

    position: fixed;
    top: 0;
}

/*scroll*/
body::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    background-color: #1E1E1E;
}

body::-webkit-scrollbar-thumb {
    border-radius: 2px;
    width: 2px;
    background-color: #359EF9;
}

body::-webkit-scrollbar-corner {
    background-color: #1E1E1E;
}

/*about*/
#back-t{
    width: 300vh;
    height: 300vh;
    background-image: url(../images/back_desk3.svg);
    background-size: cover;

    z-index: 199;
    bottom: 0;
    right: 0;
    position: fixed;
}

#back-t2{
    width: 300vh;
    height: 300vh;
    background-image: url(../images/back_desk3.svg);
    background-size: cover;

    z-index: 499;
    bottom: 0;
    right: 0;
    position: fixed;
}

#back-tc{
    width: 100%;
    height: 100%;
    background-color: #74C0A7;

    z-index: 198;
    position: fixed;
    top: 0;
    left: 0;
}

#shadowPlant {
    width: fit-content;
    height: 100vh;
    display: flex;
    flex-direction: row;
    position: fixed;
    top: 0;
    left: 100%;
    z-index: 450;
}

#shadowPlant img {
    width: calc(100vh * 400 / 1000);
    height: 100vh;
    object-fit: contain;
}

#shadow_t{
    width: 100vw;
    height: 100vh;
    background: #2D2C2A;
    margin: 0;
}

#shadow1{
    margin-right: -5px;
}

#shadow2{
    margin-left: -5px;
}