/*Estilos especificos de la seccion about*/
#about {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 70px;
    overflow: hidden;
    /*transition: transform 0.5s linear;*/
    z-index: 200;
}

#wood-wall {
    width: 100%;
    height: 60%;
    background: url(../images/overDesk.svg);
}

#desk {
    width: 100%;
    height: 40%;
    background: #AD9A86;
}

#screen-about {
    position: relative;
    top: 0;
}

#cont-visual {
    width: 100%;
    height: 100%;
    background: #D3EDD3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: opacity 0.5s ease;
}

#window_vsc {
    width: 100%;
    height: 100%;
    background: #333333;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

#bar-window_vsc {
    width: calc(100% - 6px);
    height: 4%;
    background: #3C3C3C;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 3px;
}

#bar-window_vsc div {
    height: 100%;
    width: fit-content;
}

#bar-window_vsc .btn {
    height: 70%;
    width: 70%;
    object-fit: contain;
}

#logo-vsc {
    height: 100%;
    width: fit-content;
    object-fit: contain;
}

#line {
    height: 10%;
    width: 100%;
    background: #333333;
    margin: auto 10%;
}

.size-cont {
    width: 20%;
}

#content-window_vsc {
    width: 100%;
    height: 93%;
    background: #1E1E1E;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}

#nav_vsc ul::-webkit-scrollbar, #textoMD::-webkit-scrollbar,
#nav_vsc::-webkit-scrollbar,
.code-file::-webkit-scrollbar {
    width: 2px;
    height: 2px;
    background-color: transparent;
    margin: 0 1px;
}

#nav_vsc ul::-webkit-scrollbar-thumb, #textoMD::-webkit-scrollbar-thumb,
#nav_vsc::-webkit-scrollbar-thumb,
.code-file::-webkit-scrollbar-thumb {
    border-radius: 2px;
    width: 2px;
    background-color: #359EF9;
}

#nav_vsc ul::-webkit-scrollbar-corner, #textoMD::-webkit-scrollbar-corner,
#nav_vsc::-webkit-scrollbar-corner,
.code-file::-webkit-scrollbar-corner {
    background-color: #1E1E1E;
}

#nav_vsc {
    width: calc(20% - 2px);
    height: calc(100% - 2px);
    background: #252526;
    overflow: scroll;
}

#nav_vsc ul {
    padding: 0;
    width: fit-content;
    margin: 0;
}

.file-item {
    height: 20px;
    cursor: pointer;
    
}

.file-item:hover {
    background: #3C3C3C;
}

.folder {
    width: calc(100% - 10px);
    padding: 0 5px;
}

.file {
    width: calc(100% - 20px);
    padding: 0 5px 0 15px;
}

.icon_vsc {
    height: 80%;
    object-fit: contain;
}

.file-item div {
    height: 100%;
    width: 100%;
}

.file-item p {
    height: 100%;
    margin: 0;
    padding: 0 0 0 3px;
    font-size: 0.5em;
    text-align: start;
    color: #FFFFFF;
}

#file_vsc {
    width: 80%;
    height: 100%;
}

#textoMD {
    background: #D3EDD3;
    width: 100%;
    height: 100%;
    transition: opacity 0.5s ease;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow-y: scroll;
}

#textoMD p {
    color: #000000;
    font-size: 0.6em;
    text-align: justify;
    margin: 0.6em 10%;
}

#textoMD strong {
    color: #359EF9;
}

#textoMD h2 {
    color: #000000;
    font-size: 1.2rem;
    text-align: center;
    margin: 0.6em 10%;
}

#file-name_vsc {
    width: fit-content;
    height: 20px;
    /*background: #1E1E1E;*/
    background: #161515;
}

.code-file{
    width: calc(100% - 4px);
    height: calc(100% - 4px);
    background: #1E1E1E;
    overflow: auto;
    border: 2px solid #161515;
}

.code-file p{
    font-size: 0.5em;
    margin: 0.6em;
    text-align: start;
    white-space: pre;
}

#file-content_vsc {
    width: 100%;
    height: calc(100% - 20px);
    overflow: hidden;
}

#imgView{
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    object-fit: contain;
    padding: 10px;
}

.file-win{
    width: 100%;
    height: 100%;
}

.txt-white{
    color: #FFFFFF;
}

.txt-blue{
    color: #3A8BD3;
}

.txt-yellow{
    color: #DCDC8B;
}

.txt-blue2{
    color: #9CDCFE;
}

.txt-green{
    color: #4EC9B0;
}

.txt-red{
    color: #CE723B;
}

.txt-purple{
    color: #CE70CA;
}

#p-md{
    text-align: justify;
    white-space: initial;
}

@media screen and (orientation: portrait) {
    #bar-window_vsc {
        height: 2%;
    }

    #content-window_vsc {
        height: 98%;
        flex-direction: column-reverse;
    }

    #nav_vsc {
        width: 100%;
        height: 20%;
        background: #252526;
        overflow: auto;
    }

    #file_vsc {
        width: 100%;
        height: 80%;
    }

    #nav_vsc ul {
        padding: 0;
        width: 100%;
        height: 100%;
        margin: 0;
    }

}

/*height < 300*/
@media screen and (max-height: 300px) {
    .icon_vsc{
        height: 40%;
    }
}

/*width < 300*/
@media screen and (max-width: 300px) {
    .icon_vsc{
        height: 40%;
    }

    #textoMD {
        display: block;
    }

    

    #wood-wall {
        height: 50%;
    }
    
    #desk {
        height: 50%;
    }
}
