.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.rojo {
  color: red;
}

.verde {
  color: green;
}

/*Main Menu*/
.cont_MainMenu {
  top: 0;
  position: fixed;
  z-index: 1000;
  justify-content: space-between;
  background: #FFF;
  width: 100%;
  height: 70px;
}

.logo_MainMenu {
  width: 140px;
  height: 140px;
  padding: 0 20px;
  transition: all 0.3s ease;
}

.logo_MainMenu:hover {
  width: 150px;
  height: 150px;
  transform: rotate(7deg);
  padding: 0 15px;
}

.optMenu {
  color: #000;
  font-size: 18px;
  line-height: 30px;
  width: 100px;
  height: 30px;
  border-radius: 5px;
  margin: 5px 10px;
  padding: 4px 8px;
  transition: all 0.3s ease;
  cursor: pointer;
}

.optMenu:hover {
  background: #359EF9;
  color: #FFF;
}

#lista_MainMenu,
#lista_MainMenu_R {
  height: fit-content;
  margin: auto;
  align-content: center;
  padding: 0;
}

#lista_MainMenu_R {
  margin-top: 10px;

}

#a-home img:hover	{
  cursor: pointer;
}

#a-home:hover .lb-home {
  display: block;
}

/*Btn Menu*/
input#abrir-cerrar {
  visibility: hidden;
  position: absolute;
  top: -9999px;
}

.toggle-button {
  display: block;
  width: 50px;
  height: 50px;
  /*border: 1px solid black;*/
  position: relative;
  cursor: pointer;
  box-sizing: border-box;
}

.toggle-button:before,
.toggle-button:after {
  position: absolute;
  display: block;
  content: " ";
  width: calc(100% / 2);
  box-sizing: border-box;
  left: calc(100% / 4);
  transition: all 0.2s ease-out;
}

.toggle-button:before {
  top: calc((100% / 4) + 1px);
  height: calc((100% / 4) + 1px);
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  background-color: transparent;
}

.toggle-button:after {
  height: 2px;
  background-color: black;
  bottom: calc(100% / 4);
}

input#abrir-cerrar:checked+.toggle-button:before,
input#abrir-cerrar:checked+.toggle-button:after {
  top: calc((100% / 2));
  height: 2px;
  border-bottom: 0;
}

input#abrir-cerrar:checked+.toggle-button:before {
  transform: rotate(45deg);
}

input#abrir-cerrar:checked+.toggle-button:after {
  transform: rotate(-45deg);
}

.abrir,
.cerrar,
.lb-lang,
.lb-home {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  text-indent: -9999px;
}

.abrir {
  display: block;
}

.cerrar,
.lb-lang,
.lb-home {
  display: none;
}

input#abrir-cerrar:checked+.toggle-button .abrir {
  display: none;
}

input#abrir-cerrar:checked+.toggle-button .cerrar {
  display: block;
}

/*Menu Responsive*/
.cont_menu_movil {
  display: none;
  background: #FFF;
  width: fit-content;
  height: fit-content;
  padding: 10px 20px;
  margin-right: 10px;
  border-radius: 10px;
}

@media screen and (max-width: 900px) {
  .cont_menu_movil {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

  }

  #lista_MainMenu {
    display: none;
  }
}

@media screen and (max-width: 370px) {
  .logo_MainMenu {
    padding: 0 5px;
    width: 100px;
    height: 100px;
  }

  .logo_MainMenu:hover {
    padding: 0;
    width: 110px;
    height: 110px;
  }

  #lista_MainMenu {
    display: none;
  }
}

/*language*/
#cont-language {
  bottom: 10px;
  right: 10px;
  position: fixed;
  z-index: 999;
  width: 60px;
  height: 60px;
  opacity: 0.6;
  transition: all 0.6s ease;
}

#cont-language:hover {
  width: 65px;
  height: 65px;
  opacity: 1;
}

#cont-language:hover .lb-lang {
  display: block;
}

.cont-circle {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}

.cont-circle img {
  width: 85%;
  height: 85%;
  border-radius: 50%;
  margin: auto;
  object-fit: cover;
}