/*Estilos especificos de la seccion contact*/
#contact{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 70px;
    left: 0;
    z-index: 700;
}

#window_contact{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

#content-window_contact{
    width: 80%;
    height: fit-content;
    margin: auto;
}

#window_contact a {
    text-decoration: none;
}

#content-window_contact img {
    width: 20%;
    height: 20%;
    margin: 5%;
    object-fit: contain;
}

#content-window_contact img:hover {
    cursor: pointer;
}

#content-window_contact p {
    text-align: justify;
    font-size: 0.6em;
    color: black;
    word-wrap: break-word;
}

#content-window_contact h3 {
    font-size: 0.6em;
    text-align: center;
    word-wrap: break-word;
}

#content-window_contact strong, #content-window_contact h3 {
    color: #359EF9;
}