/*Estilos especificos de la seccion home*/
#home {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    transition: transform 0.5s ease-in-out;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

#fondo_home {
    width: max(100%, calc(100vh * 16 / 9));
    height: 100vh;
}

#trees {
    width: 100%;
    height: 100%;
    background: #88D9ED;
}

#room {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
    top: -200vh;
    margin: 0;
}

#tree1 {
    animation: air 15s infinite;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#tree2 {
    animation: air2 15s infinite;
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
    top: -100%;
}

@keyframes air {
    0% {
        transform: translate(0, 0);
    }

    25% {
        transform: translate(-15px, 5px);
    }

    50% {
        transform: translate(0, 0);
    }

    75% {
        transform: translate(-15px, 5px);
    }
}

@keyframes air2 {
    0% {
        transform: translate(0, 0);
    }

    25% {
        transform: translate(0, -15px);
    }

    50% {
        transform: translate(0, 0);
    }

    75% {
        transform: translate(0, -15px);
    }
}

#chair {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
    top: -300vh;
    transition: transform 0.5s ease;
    z-index: 190;
}