/*Estilos especificos de la seccion proyects*/
#projects{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 70px;
    left: 0;
    z-index: 400;
}

#window_projects{
    width: 100%;
    height: 100%;
    background: #333333;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

#bar-window_exp {
    width: calc(100% - 6px);
    height: 4%;
    background: #3C3C3C;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 3px;
}

#bar-window_exp div {
    height: 100%;
    width: fit-content;
}

#bar-window_exp .btnw {
    height: 70%;
    width: 70%;
    object-fit: contain;
}

#logo-exp {
    height: 100%;
    width: fit-content;
    object-fit: contain;
}

#content-window_exp {
    width: 100%;
    height: 93%;
    background: #1E1E1E;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}

#nav_exp::-webkit-scrollbar {
    width: 4px;
    height: 4px;
    background-color: transparent;
    margin: 0 1px;
}

#nav_exp::-webkit-scrollbar-thumb {
    border-radius: 2px;
    width: 4px;
    background-color: #359EF9;
}

#nav_exp::-webkit-scrollbar-corner {
    background-color: #1E1E1E;
}

#nav_exp {
    width: calc(20% - 2px);
    height: calc(100% - 2px);
    background: #252526;
    overflow: scroll;
}

#nav_exp ul {
    padding: 0;
    width: fit-content;
    margin: 0;
}

#file_exp {
    width: 80%;
    height: 100%;
}

.file-item {
    height: 20px;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
}

.file-item:hover {
    background: #303030;
}

.folder {
    width: calc(100% - 10px);
    padding: 0 5px;
}

.selected-project {
    background: #3C3C3C; 
}

.file {
    width: calc(100% - 20px);
    padding: 0 5px 0 15px;
}

.icon_exp {
    height: 80%;
    object-fit: contain;
}

.file-item div {
    height: 100%;
    width: 100%;
}

.file-item p {
    height: 100%;
    margin: 0;
    padding: 0 0 0 3px;
    font-size: 0.5em;
    text-align: start;
    color: #FFFFFF;
}

.cont-project {
    width: 90%;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5%;
}

.cont-folder {
    width: 90%;
    height: 85%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    padding: 5%;
}

.cont-file a {
    text-decoration: none;
    color: #FFFFFF;
}

.cont-file p {
    margin: 0;
    font-size: 0.5em;

}

.cont-file {
    width: 25%;
    height: 30%;
}

.cont-file:hover {
    background: #3C3C3C;
    cursor: pointer;
}

.img-file-folder{
    width: 100%;
    height: 70%;
    object-fit: contain;
}

.cont-image, .cont-readme {
    width: 94%;
    height: 90%;
    padding: 3%;
}

.cont-readme {
    overflow-y: auto;
}

.cont-readme::-webkit-scrollbar, .cont-project-detail::-webkit-scrollbar {
    width: 4px;
    height: 4px;
    background-color: transparent;
    margin: 0 1px;
}

.cont-readme::-webkit-scrollbar-thumb, .cont-project-detail::-webkit-scrollbar-thumb {
    border-radius: 2px;
    width: 4px;
    background-color: #359EF9;
}

.cont-readme::-webkit-scrollbar-corner, .cont-project-detail::-webkit-scrollbar-corner {
    background-color: #1E1E1E;
}

.cont-readme p {
    margin: 0;
    font-size: 0.5em;
    text-align: justify;
    color: #FFFFFF;
    white-space: pre-line;
}

.img-preview {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.cont-project-detail {
    /*background: #0e7ee0;*/
    background: #005eb1;
    width: 90%;
    height: 90%;
    padding: 5%;
    overflow-y: auto;
}

.title {
    margin: 0 0 8px 0;
    color: white;
    font-size: 1.4rem;
    font-weight: bold;
}



.title-desc {
    padding: 10px;
    background: rgba( 255, 255, 255, 0.45 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7px );
    -webkit-backdrop-filter: blur( 7px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.badge {
    padding: 0.25em 0.5em;
    font-size: 0.75rem;
    font-weight: bold;
    color: white;
    border-radius: 8px; /* Hace que el badge sea ligeramente redondeado */
    vertical-align: middle;
    margin: 0 3px;
    white-space: nowrap;
}

.techs {
    overflow-x: auto;
    align-items: center;
    justify-content: flex-start;
    padding: 4px;
    background: rgba(32, 32, 32, 0.05);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    margin-bottom: 8px;
}

.techs::-webkit-scrollbar {
    height: 4px;
    background-color: transparent;
    margin: 0 1px;
}

.techs::-webkit-scrollbar-thumb {
    border-radius: 2px;
    width: 4px;
    background-color: #359EF9;
}

.techs::-webkit-scrollbar-corner {
    background-color: #1E1E1E;
}

.btn {
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 0.85rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    margin: 5px;
}

.btn-light {
    color: #212529;
    background-color: #f8f9fa;
    border-color: #f8f9fa;
}

.btn-light:hover {
    color: #212529;
    background-color: #e2e6ea;
    border-color: #dae0e5;
}

.desc p {
    margin: 16px 0;
    font-size: 0.85rem;
    text-align: justify;
}

.photo-idx {
    font-size: 1.75rem;
    margin: 0 5px;
    color: #a1a1a1;
    cursor: pointer;
}

.photo-idx-select{
    color: white;
}

@media screen and (orientation: portrait) {
    #bar-window_exp {
        height: 2%;
    }

    #content-window_exp {
        height: 98%;
        flex-direction: column-reverse;
    }

    #nav_exp {
        width: 100%;
        height: 20%;
        background: #252526;
        overflow: auto;
    }

    #nav_exp ul {
        padding: 0;
        width: 100%;
        height: 100%;
        margin: 0;
    }

    #file_exp {
        width: 100%;
        height: 80%;
    }
}

@media screen and (max-width: 400px) {
    #btn-back {
        opacity: 0;
        z-index: -1;
    }

    .title {
        font-size: 0.8rem;
    }
}